import { getActClass } from './utils';


export default {
  name: 'PressActStoreItem',
  options: {
    styleIsolation: 'shared',
  },
  props: {
    propsData: {
      type: Object,
      default: () => ({}),
    },
    useTipClass: {
      type: Boolean,
      default: false,
    },
    hideTipStyle: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    getPrivilegeStyle(privilege) {
      if (privilege) {
        switch (privilege) {
          case 1:
            return this.getActClass('icon--jp');
          case 2:
            return this.getActClass('icon--yp');
          default:
            return '';
        }
      }
      return '';
    },
    getStateStyle(state) {
      switch (state) {
        case 'instore':
          return this.getActClass('icon--instore');
        case 'eventbeen':
          return this.getActClass('icon--eventbeen');
        case 'lately':
          return this.getActClass('icon--lately');
        default:
          return '';
      }
    },
    onClickItem() {
      this.$emit('clickItem');
    },
    getActClass(...args) {
      return getActClass(this.useTipClass, args);
    },
  },
};
